<template>
  <div>
    <div class="fabFaqWrap" :style="`bottom:${btnBottom2}px;right:${btnRight2}px`" v-on="fabmove()">
      <img src="../../images/faqBtn.gif" />
      <!--@click="eventbtn2"-->
      <!-- <section class="eventPopup2" v-if="eventPopup2">test..test</section> -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // faq FAB(플로팅버튼)좌표
      btnBottom2: 80,
      btnRight2: 20
    }
  },
  props: {
    backPopup: Boolean,
    eventPopup2: Boolean
  },
  methods: {
    eventbtn2() {
      this.$emit('eventbtn2')
    },

    //FabEventPopup 과 해당FabFaqPopup이 열렸을때 버튼이동 메소드
    fabmove() {
      if (this.backPopup == true) {
        //FabEventPopup 과 해당FabFaqPopup이 열렸을때 이벤트버튼과 나란히 옮겨지게 변수값변경 btnBottom2을 FabEventPopup에서 btnBottom값이랑 동일하게 변경해주셔야 이벤트버튼 과 나란한 위치로 변경됩니다.
        this.btnBottom2 = 20
        this.btnRight2 = 80
      } else {
        //FabEventPopup 과 해당FabFaqPopup이 꺼졌을때 원래 버튼위치로 data에 btnBottom2,btnRight2같은 값으로 맞춰주셔야 원래위치로 돌아갑니다.
        this.btnBottom2 = 80
        this.btnRight2 = 20
      }
    }
  }
}
</script>

